.Navbar{display:flex;justify-content:flex-end;height:auto;background:linear-gradient(90deg,#38adae,#cd295a);text-align:right}.NavLeft{justify-content:flex-start}.NavLeft,.NavRight{display:inherit;padding:20px;color:#fff;align-items:center;text-align:center;width:100%}.NavRight{justify-content:flex-end}.NavRight a{text-decoration:none;margin:0 20px;color:#fff}.NavRight span{margin:0 20px;color:#fff;cursor:pointer}.Navbar .active,.Navbar .NavRight span:active,.Navbar .NavRight span:hover{text-decoration:underline;color:#38adae}@media only screen and (max-width:599px){.Navbar{font-size:.5em}.Navbar span,.NavRight a{margin:0 5px}}