.StudentInfo{display:flex;flex-direction:column;width:90%;height:auto;background-color:#38adae;margin:auto;overflow:auto;box-shadow:2px 2px 2px 2px rgba(0,0,0,.5);box-sizing:border-box}.StudentInfo h3{color:#fff;margin-top:10px;margin-bottom:20px;margin-left:10px}.StudentInfo span{color:#fff;margin-left:10px;font-size:16px}.StudentInfo button{height:auto;background-color:#cd295a;color:#fff;outline:none;cursor:pointer;width:100px;margin:30px 0 30px 10px;border-color:#cd295a;box-shadow:2px 2px 2px rgba(109,19,19,.5)}@media only screen and (max-width:599px){.StudentInfo span{font-size:12px}.StudentInfo button{width:50px}}