.StudentInfoEdit{display:flex;flex-direction:column;width:90%;height:300px;background-color:#38adae;margin:auto;overflow:auto;box-sizing:border-box;border:3px solid #ccc}.StudentInfoEdit h3{color:#fff;margin-top:10px;margin-bottom:20px;margin-left:10px}.StudentInfoEdit__Button{font:inherit;display:inline-block;width:100px;height:20px;margin:10px;cursor:pointer;font-size:10px;outline:none;color:#fff;background-color:#cd295a;border-color:#cd295a;box-shadow:2px 2px 2px rgba(109,19,19,.5)}.AlignInputField{display:inline-block;border:1px solid #ccc;padding:5px}.AlignInputField span{text-transform:capitalize;color:#fff;padding:5px}@media only screen and (max-width:599px){.StudentInfoEdit__Button{width:50px}}